import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params, Data } from '@angular/router';

/* Services */
import { StatisticsService } from '../../../Services/Statistics/statistics.service'

export const fakePercentage = {
	'42': ['55%', '22%', '18%', '5%'],
	'ESCP': ['40%', '32%', '21%', '7%'], 
	'Dauphine': ['39%', '27%', '21%', '13%'], 
	'The_Refiners': ['60%', '27%', '9%', '5%']
}

@Component({
	selector: 'app-network-projects',
	templateUrl: './network-projects.component.html',
	styleUrls: ['./network-projects.component.scss']
})
export class NetworkProjectsComponent implements OnInit {

	statistics 	: any
	fakeList 	: any

	constructor(private StatisticsService: StatisticsService,
				private Router: ActivatedRoute,
				private route: Router) { }

	ngOnInit() {
		this.initParams();
	}

	initParams() {
		this.Router.parent.params.subscribe( params => {
			if (params && params['name']) {
				this.getNetworkProjectsStatistics(params['name']);
			} else
				this.route.navigate(['/']);
		});
	}

	getNetworkProjectsStatistics(network) {
		this.StatisticsService.getNetworksProjects(network).subscribe( res => {
			this.statistics = res
			console.log(this.statistics);
			this.fakeList 	= fakePercentage[network]; 
		});
	}

	getWidth(index) {
		if (!index) return '100%'
		else if (index === 1) return '90%'
		else if (index === 2) return '80%'
		else if (index === 3) return '65%'
	}

	getPercentage(number, type) {
		let percentage = 0

		percentage = Math.round((number * 100) / this.statistics[type].length);
		return percentage
	}
}



