import { Component, OnInit, Input, trigger, state, style, animate, transition, keyframes,  } from '@angular/core';
import { Router, ActivatedRoute, Params, Data } from '@angular/router';

/* Services */
import { StatisticsService } from '../../../Services/Statistics/statistics.service'

/* Interfaces */
import { fake42, fakeESCP, fakeDauphine, fakeRefiners } from '../../../Interfaces/Constants/fake.constant';

/* Animations */

@Component({
	selector: 'app-network-profiles',
	templateUrl: './network-profiles.component.html',
	styleUrls: ['./network-profiles.component.scss'],
	// animations: [
	// 	trigger('dialog', [
	// 		transition('void => *', [
	// 			style({ maxHeight: "100px" }),
	// 			animate("400ms")
	// 		]),
	// 		transition('* => void', [
	// 			animate("400ms", style({ maxHeight: '100px' }))
	// 		])
	// 	])
	// ]
})
export class NetworkProfilesComponent implements OnInit {

	statistics 		: any
	statSkills 		: any
	statInterests 	: any
	locations 		: any
	showCountry 	: number
	activities 		: any

	constructor(private StatisticsService: StatisticsService,
				private Router: ActivatedRoute,
				private route: Router) { }

	ngOnInit() {
		this.showCountry = 0
		this.initParams();
	}

	/* FAKE */
	initFake(name) {
		if (name === '42') {
			this.locations 		= fake42[0].locations
			this.statSkills 	= fake42[0].skills
			this.statInterests 	= fake42[0].interests
			this.activities 	= fake42[0].activities
		} else if (name === 'ESCP') {
			this.locations 		= fakeESCP[0].locations
			this.statSkills 	= fakeESCP[0].skills
			this.statInterests 	= fakeESCP[0].interests
			this.activities 	= fakeESCP[0].activities
		} else if (name === 'Dauphine') {
			this.locations 		= fakeDauphine[0].locations
			this.statSkills 	= fakeDauphine[0].skills
			this.statInterests 	= fakeDauphine[0].interests
			this.activities 	= fakeDauphine[0].activities
		} else {
			this.locations 		= fakeRefiners[0].locations
			this.statSkills 	= fakeRefiners[0].skills
			this.statInterests 	= fakeRefiners[0].interests
			this.activities 	= fakeRefiners[0].activities
		}

	}

	initParams() {
		this.Router.parent.params.subscribe( params => {
			if (params && params['name']) {
				this.initFake(params['name']);
				this.getNetworkProfilesStatistics(params['name']);
			} else
				this.route.navigate(['/']);
		});
	}

	getCountry(index) {
		this.showCountry = index
	}

	getInfo(id, type) {
		const length = this.statistics['profiles'].length

		for (let i = 0; i < length; i++) {
			if (this.statistics['profiles'][i].id === id)
				return type === 'picture' ? this.statistics['profiles'][i].profile_picture
											: this.statistics['profiles'][i].fullName
		}
	}

	// getRandomProfile() {
	// 	let random = Math.floor((Math.random() * this.statistics['profiles'].length) + 1)
	// 	console.log(random);
	// }

	getNetworkProfilesStatistics(network) {
		this.StatisticsService.getNetworksProfiles(network).subscribe( res => {
			console.log(res);
			this.statistics = res
			// this.getRandomProfile();
			// this.statSkills = this.statistics.skills.slice(0, 4);
		});
	}

	getPercentage(number, type) {
		let percentage = 0

		percentage = Math.round((number * 100) / this.statistics[type].length);
		return percentage
	}

	getWidth(index, percentage, type) {
		if (index === 0) {
			return '100%'
		} else {
			if (type === 'skills')
				return ((percentage * 100) / this.statSkills[0].percentage) + '%'
			else
				return ((percentage * 100) / this.statInterests[0].percentage) + '%'
		}
	}
}



