import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { FooterModule } from '../footer/footer.module';

/* Components */
import { NetworksComponent } from './networks.component';
import { NetworkProjectsComponent } from './network-projects/network-projects.component';
import { NetworkProfilesComponent } from './network-profiles/network-profiles.component';

/* Librairies */
import { MomentModule } from 'angular2-moment';

/* Route */
import { NETWORK_ROUTES } from '../../Routes/networks.routes';

@NgModule({
	imports: [
		NETWORK_ROUTES,
		CommonModule,
		RouterModule,
		MomentModule,
		FooterModule
	],
	declarations: [
		NetworksComponent,
		NetworkProjectsComponent,
		NetworkProfilesComponent
	]
})
export class NetworksModule { }



