/* Modules */
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

/* Components */
import { NetworksComponent } from '../Components/networks/networks.component';
import { NetworkProjectsComponent } from '../Components/networks/network-projects/network-projects.component';
import { NetworkProfilesComponent } from '../Components/networks/network-profiles/network-profiles.component';

/* Route */
const routes: Routes = [
	{ path: '', component: NetworksComponent,
		children: [
			{ path: '', redirectTo: 'profiles', pathMatch: 'full' },
			{ path: 'profiles', component: NetworkProfilesComponent },
			{ path: 'projects', component: NetworkProjectsComponent }
		] 
	},
];

export const NETWORK_ROUTES: ModuleWithProviders = RouterModule.forChild(routes);



