import { Component, OnInit, Input, trigger, state, style, animate, transition, keyframes, } from '@angular/core';
import { Router, ActivatedRoute, Params, Data } from '@angular/router';

/* Services */
import { StatisticsService } from '../../Services/Statistics/statistics.service'

/* Interfaces */
import { fake42, fakeESCP, fakeDauphine } from '../../Interfaces/Constants/fake.constant';

@Component({
	selector: 'app-networks',
	templateUrl: './networks.component.html',
	styleUrls: ['./networks.component.scss'],
	// host: {
	// 	'[@slide]'	: "inactive",
	// 	'[@rotate]'	: "down"
	// },
	animations: [
		trigger('slide', [
			state('inactive', style({
				maxHeight: '105px', overflow: 'hidden'
			})),
			state('active',   style({
				maxHeight: '10000px', overflow: 'auto'
			})),
			transition('inactive => active', animate('800ms ease-in')),
			transition('active => inactive', animate('800ms ease-out'))
	    ]),
	    trigger('slideFilter', [
			state('inactive', style({
				maxHeight: '105px', overflow: 'hidden'
			})),
			state('active',   style({
				maxHeight: '10000px', overflow: 'auto'
			})),
			transition('inactive => active', animate('800ms ease-in')),
			transition('active => inactive', animate('800ms ease-out'))
	    ]),
	    trigger('rotate', [
	    	state('down', style({
	    		transform: 'rotate(0)'
	    	})),
	    	state('up', style({
	    		transform: 'rotate(180deg)'
	    	})),
	    	transition('inactive => active', animate(200)),
			transition('active => inactive', animate(200))
	    ])
	],
	providers: [StatisticsService]
})
export class NetworksComponent implements OnInit {

	slideState 		: string
	rotateState 	: string
	projects 		: number
	profiles 		: number
	network 		: Object
	showFilterText 	: boolean = true
	networkName 	: string
	fake_profiles 	: number

	constructor(private StatisticsService: StatisticsService,
				private Router: ActivatedRoute,
				private route: Router) {
		this.slideState 	= 'inactive'
		this.rotateState 	= 'down'
		this.initParams();
	}

	ngOnInit() {}

	initFake(name) {
		if (name === '42') {
			this.fake_profiles = fake42[0].users
		} else if (name === 'ESCP') {
			this.fake_profiles = fakeESCP[0].users
		} else if (name === 'Dauphine') {
			this.fake_profiles = fakeDauphine[0].users
		} else {
			this.fake_profiles = 2
		}
	}

	initServices() {
		this.getNetworkGroup();
		this.getNetworkProfilesStatistics();
		this.getNetworkProjectsStatistics();
	}

	initParams() {
		this.Router.params.subscribe( params => {
			if (params && params['name']) {
				this.networkName = params['name'];
				this.initFake(params['name']);
				this.initServices();
			} else
				this.route.navigate(['/']);
		});
	}

	getNetworkGroup() {
		this.StatisticsService.getNetworkGroup(this.networkName).subscribe( res => {
			if (res.project)
				this.network = res.project[0].network_data[0]
		});
	}

	getNetworkProfilesStatistics() {
		this.StatisticsService.getNetworksProfiles(this.networkName).subscribe( res => {
			this.profiles = res.profiles
		});
	}

	getNetworkProjectsStatistics() {
		this.StatisticsService.getNetworksProjects(this.networkName).subscribe( res => {
			this.projects = res
		});
	}

	showText() {
		this.slideState 	= this.slideState 	=== 'inactive' ? 'active' : 'inactive'
		this.rotateState 	= this.rotateState 	=== 'down' ? 'up' : 'down'
		if (this.showFilterText) {
			this.showFilterText = false
		} else {
			setTimeout(() => {
				this.showFilterText = true;
			}, 800)
		}
	}

	replace(name) {
		return name.replace('_', ' ');
	}
}



